import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import styles from './ProjectDetail.module.scss';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import ProjectIntro from '../../components/ProjectDetail/ProjectIntro/ProjectIntro';
import ProjectAction from '../../components/ProjectDetail/ProjectAction/ProjectAction';
import { useProjectInfoByHandleWithDonorInfo } from '../../hooks/useProjectInfo';

const ProjectDetail = () => {
  const [project, setProject] = useState();
  const history = useHistory();
  const param = useParams();
  const { projectId } = param;

  const projectPromise = useProjectInfoByHandleWithDonorInfo(projectId);
  useEffect(() => {
    projectPromise
      .then((data) => {
        setProject(data);
      })
      .catch((err) => {
        history.replace('/projects');
        console.log(err);
      });
  }, [projectPromise]);

  return (
    <div className={styles.ProjectDetail}>
      <MainContainer>
        {!project && 'Loading'}
        {project && (
          <>
            <ProjectIntro item={project} />
            <br />
            <br />
            <ProjectAction item={project} />
          </>
        )}
      </MainContainer>
    </div>
  );
};

export default ProjectDetail;
