import { useEffect, useMemo, useState } from 'react';
import { ethers, utils } from 'ethers';
import { useGoFundUsManagerContract, useMulticallContract } from './useContract';
import GO_FUND_US_MANAGER_ABI from '../constants/abis/GO_FUND_US_MANAGER.json';
import { multicallHelper } from '../utils/multicall';
import { decodeBytesToObject, getTokenName } from '../utils/utils';
import { useWallet } from './useWallet';
import { useAccountTokenBalance } from './useAccountBalance';

export const decodeDistributions = (distributions) =>
  distributions.map((item) => ({
    perMillion: item.perMillion.toNumber(),
    processed: item.processed,
    unlockTimestamp: item.unlockTimestamp.toNumber(),
    who: item.who,
  }));
export const decodeProjectInfo = (projectInfo, account) => {
  const price = projectInfo.price.toString();
  const totalDonation = utils.formatEther(projectInfo.totalDonation);
  const tokenTotalSupply = totalDonation * price;
  const now = +new Date() / 1000;
  const donationDeadline = projectInfo.donationDeadline.toNumber();
  const reservePerMillion = projectInfo.reservePerMillion.toNumber();
  return {
    actionBriefCursor: projectInfo.actionBriefCursor.toNumber(),
    actionBriefLength: projectInfo.actionBriefLength.toNumber(),
    actionBriefs: projectInfo.actionBriefs, // Array
    description: decodeBytesToObject(projectInfo.description),
    donationCap: utils.formatEther(projectInfo.donationCap),
    donationDeadline,
    donationDistribution: decodeDistributions(projectInfo.donationDistribution),
    donationTaken: projectInfo.donationTaken,
    donationToken: projectInfo.donationToken,
    donationTokenName: getTokenName(projectInfo.donationToken),
    id: utils.parseBytes32String(projectInfo.id),
    minimumDonationAmount: utils.formatEther(projectInfo.minimumDonationAmount),
    mintable: projectInfo.mintable,
    price,
    projectName: projectInfo.projectName,
    projectOwner: projectInfo.projectOwner,
    refundable: projectInfo.refundable,
    reserveDistribution: decodeDistributions(projectInfo.reserveDistribution),
    reservePerMillion,
    supportOverDonation: projectInfo.supportOverDonation,
    targetTaken: projectInfo.targetTaken,
    targetToken: projectInfo.targetToken,
    tokenName: projectInfo.tokenName,
    tokenSymbol: projectInfo.tokenSymbol,
    totalDonation: utils.formatEther(projectInfo.totalDonation),
    totalDonationDistributionPerMillion: projectInfo.totalDonationDistributionPerMillion.toNumber(),
    totalReserveDistributionPerMillion: projectInfo.totalReserveDistributionPerMillion.toNumber(),
    isOwner: account && account.toLowerCase() === projectInfo.projectOwner.toLowerCase(),
    tokenTotalSupply,
    active: now < donationDeadline || donationDeadline === 0,
    reservePercent: reservePerMillion / 10000,
  };
};
export const decodeDonorInfo = (donorInfo) => ({
  donationAmount: utils.formatEther(donorInfo.donationAmount),
  inviter: donorInfo.inviter,
  totalInviteesAmount: utils.formatEther(donorInfo.totalInviteesAmount),
  claimed: donorInfo.claimed,
  claimAmount: utils.formatEther(donorInfo.claimAmount),
  refundAmount: utils.formatEther(donorInfo.refundAmount),
  claimWithBurned: donorInfo.claimWithBurned,
  claimable: !donorInfo.claimed,
});
export const useProjectInfoByHandle = (handle) => {
  const gfuManagerContract = useGoFundUsManagerContract();
  const { account } = useWallet();
  return useMemo(async () => {
    try {
      if (!gfuManagerContract || !handle) {
        return null;
      }
      const projectInfo = await gfuManagerContract.projects(utils.formatBytes32String(handle));

      return decodeProjectInfo(projectInfo, account);
    } catch (e) {
      console.log(e);
      return null;
    }
  }, [gfuManagerContract, handle, account]);
};
export const useProjectInfoByHandleWithDonorInfo = (handle) => {
  const projectInfoPromise = useProjectInfoByHandle(handle);
  const gfuManagerContract = useGoFundUsManagerContract();
  const { account } = useWallet();
  const [count, setCount] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setCount((prev) => prev + 1);
    }, 10000);
    return () => {
      clearInterval(timer);
    };
  }, []);

  return useMemo(async () => {
    try {
      console.log('updated');
      if (
        !gfuManagerContract ||
        !gfuManagerContract.donations ||
        !handle ||
        !account ||
        !projectInfoPromise
      ) {
        return null;
      }
      const donorInfo = await gfuManagerContract.donations(
        utils.formatBytes32String(handle),
        account,
      );
      const projectInfo = await projectInfoPromise;
      const claimable = await gfuManagerContract.claimAble(
        utils.formatBytes32String(handle),
        account,
      );

      return {
        ...projectInfo,
        donorInfo: {
          ...decodeDonorInfo(donorInfo),
          claimableAmount: utils.formatEther(claimable.targetAmount),
        },
      };
    } catch (e) {
      console.log(e);
      return null;
    }
  }, [gfuManagerContract, handle, account, projectInfoPromise, count]);
};

export const useAllProjectsInfo = () => {
  const multicallContract = useMulticallContract();
  const gfuManagerContract = useGoFundUsManagerContract();
  const { account } = useWallet();
  const projectAmountPromise = useMemo(async () => {
    const amount = await gfuManagerContract.projectAmount();
    return amount.toNumber();
  }, [gfuManagerContract]);

  const callPromise = useMemo(async () => {
    const projectAmount = await projectAmountPromise;
    if (!projectAmount || Number.isNaN(projectAmount) || !gfuManagerContract.address) {
      return [];
    }
    const input = [];
    let offset = 0;
    for (let i = 0; i < projectAmount; ) {
      input.push([gfuManagerContract.address, 'projectList', [offset, 100]]);
      offset += 100;
      i += 100;
    }
    return input;
  }, [gfuManagerContract, projectAmountPromise]);

  return useMemo(async () => {
    const calls = await callPromise;
    if (!multicallContract || calls.length === 0) {
      return null;
    }
    const results = await multicallHelper(multicallContract, GO_FUND_US_MANAGER_ABI.abi, calls);
    if (!results || !results[0] || !results[0][0]) {
      return null;
    }
    return results[0][0].map((item) => decodeProjectInfo(item, account));
  }, [multicallContract, callPromise, account]);
};
